import React from "react";
import clsx from "clsx";

import stars from "./assets/stars.svg";
import cup from "./assets/cup.webp";
import css from "./style.module.scss";

import salut_1 from "./assets/salut_1.svg";
import salut_2 from "./assets/salut_2.svg";

export const Prize = () => {
  return (
    <>
      <div className={clsx(css.lightsDiv, css.lightsElements)} />

      <img
        src={stars}
        alt="stars"
        className={clsx(css.stars, css.lightsElements)}
      />
      <img
        src={salut_1}
        alt="salut_1"
        className={clsx(css.salut_1, css.lightsElements)}
      />

      <img
        src={salut_2}
        alt="salut_2"
        className={clsx(css.salut_2, css.lightsElements)}
      />

      <img src={cup} alt="cup" className={clsx(css.cup, css.lightsElements)} />
    </>
  );
};
