import React from "react";
import clsx from "clsx";

import subTitle from "./assets/subTitle.svg";
import button from "./assets/button.svg";
import title from "./assets/title.svg";
import css from "./style.module.scss";

export const Title = () => {
  return (
    <>
      <img
        className={clsx(css.title, css.titleElements)}
        src={title}
        alt="title"
      />
      <img
        className={clsx(css.subTitle, css.titleElements)}
        src={subTitle}
        alt="subTitle"
      />
      <img
        className={clsx(css.button, css.titleElements)}
        src={button}
        alt="button"
      />
    </>
  );
};
