import React, { FC } from "react";
import clsx from "clsx";

import { Prize, Title } from "./components";
import css from "./style.module.scss";

interface Props {
  play: boolean;
}

const WageringRaceBanner: FC<Props> = ({ play }) => {
  return (
    <div className={clsx(css.container, !play && css.disable)}>
      <Title />
      <Prize />
    </div>
  );
};

export default WageringRaceBanner;
